.form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  margin-bottom: 40px;
  position: relative;
  padding: 50px 50px 40px 50px;
  box-shadow: 1px 1px 2px #a7a7a7;
  @media screen and (min-width: 992px) {
    margin: 55px auto 0 0;
    position: sticky;
    top: 20px;
  }
  &::before {
    position: absolute;
    width: 100%;
    height: 100%;
    content: "";
    top: 0;
    right: 0;
    border: 1px solid #6b6b6b;
    z-index: -1;
  }
}
